<template>
    <div class="message-info" v-loading="loading">
        <div class="content">
            <div class="msg-tit">
                {{ megInfo.title }}
            </div>
            <div class="msg-time">
                {{ $L('时间：') }}
                {{ megInfo.createTime }}
            </div>
            <div class="msg-info" v-html="megInfo.message">
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "messageInfo",
        data () {
            return {
                loading: true,
                megInfo:{}
            }
        },
        created (){
          this.getData();
        },
        methods : {
            backFunc(){
                this.$router.push({name: 'messageList'});
            },
            getData () {
                if(this.$route.params.hasOwnProperty('id')){
                    const _self = this;
                    _self.loading = true;
                    let id = _self.$route.params.id;
                    let arr = {
                        messageId : id
                    };
                    _self.$api('readMessageInfo', arr , function (reData) {
                        if (reData.flag === 1) {
                            _self.megInfo = reData.data;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'messageList'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'messageList'});
                        });
                    });
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    .message-info{
        position: absolute;
        width: 100%;
        padding: 10px 0;
        left: 0;
        top: 0;
        .content{
            background: #fff;
            padding: 20px 10px;
            .msg-tit{
                font-size: 14px;
                text-align: center;
                font-weight: 700;
            }
            .msg-time{
                text-align: center;
                padding-top: 15px;
            }
            .msg-info{
                padding: 10px 0;
            }
        }
        .bottom{
            text-align: right;
            padding: 20px 0;
            border-top: 1px solid #efefef;
            background: #fff;
            .btn{
                padding: 10px 30px;
            }
        }

    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>